@use '../1-helpers/variables' as v;

/* Header */
.header {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: v.$z-fixed;
  background: v.$clr-background;
  box-shadow: 0 -4px 9px rgba(0 0 0 /.4);
  transition: .3s ease-in-out;
}

.nav {
  margin: 0 !important;
  max-width: v.$bp-laptop;
  max-height: v.$header-h;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2.3vh 1.5rem;
}

.nav-mobile__toggle {
  font-weight: normal;
  font-size: v.$icon-size * 1.1;
  cursor: pointer;
  &:hover{
    color: v.$clr-primary;
  }
}

.nav__logo {
  cursor: pointer;
  max-width: 19vw;
  #logo-header {
    max-width: 10rem;
    min-width: 90px;
    width: 100%;
    height: 100%;
  }
}

button.accessibility-toggle,
button#dark-mode-toggle-header {
  background: transparent;
  border: none;
  color: v.$clr-text;
  font-weight: normal;
  font-size: v.$icon-size * 1.3;
  cursor: pointer;
  &:hover {
    color: v.$clr-primary;
  }
}

.service__items {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding-bottom: 1rem;
}


.nav__menu {
  /* TODO*/
  position: fixed;
  bottom: -100%;
  left: 0;
  width: 100%;
  background: v.$clr-background;
  padding: 2rem 1.5rem 4rem;
  box-shadow: 0 -4px 9px rgba(0 0 0 /.3);
  border-radius: 1.5rem 1.5rem 0 0;
  transition: .3s;
}

.nav__list {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  &:hover {
    color: v.$clr-primary;
  }
  /*display: none;*/
}

.accessibility__settings {
  //TODO
  display: none;
}

.nav__icon {
  font-size: v.$icon-size;
}

.nav__close {
  position: absolute;
  right: 1.6rem;
  bottom: 1.5rem;
  font-size: v.$icon-size;
  cursor: pointer;
  color: v.$clr-primary;
  &:hover {
    color: v.$clr-text;
  }
}

.show__menu {
  bottom: 0;
}

.headerSticky {
  background-color: #141b2b;
  transition: background-color 0ms .4s, transform .4s;
  transform: translateY(0);
  box-shadow: 0 2px 4px rgba(20, 27, 43, 0.6);
}

.headerHidden {
  background-color: #141b2b;
  transform: translateY(-92px);
  transition: background-color 0ms .4s, transform .4s;
}